import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AppService {

  constructor(private http:HttpClient) { }

  fetchPhotos=(skip):Observable<any>=>{
    return this.http.get(`https://api.flickr.com/services/rest/?method=flickr.photos.search&api_key=c322d705e685edd1144876851dbb1474&text=food&extras=description%2Cowner_name%2Curl_s&per_page=30&page=${skip}&format=json&nojsoncallback=1`)    
  }
  public setInfoInLocalStorage=(data)=>{
    localStorage.setItem('photos',JSON.stringify(data));
  }
  public getInfoFromLocalStorage=()=>{
    return JSON.parse(localStorage.getItem('photos'));
  }
}
