import { Component, OnInit } from '@angular/core';
import { AppService } from '../app.service';
import { ActivatedRoute } from '@angular/router';
import { Review } from './Review';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-info',
  templateUrl: './info.component.html',
  styleUrls: ['./info.component.css']
})
export class InfoComponent implements OnInit {
  public pic:{};
  private id:string;
  private photos=[];
  public index:number;

  public name:string;
  public review:string;
  public rating:number=0;

  constructor(private appService:AppService,private route:ActivatedRoute,private toastr: ToastrService) { }

  ngOnInit() { 
    this.id=this.route.snapshot.paramMap.get('id');
    this.photos=this.appService.getInfoFromLocalStorage();
    this.fetchInfo();
   }
  fetchInfo=()=>{
    this.index=this.photos.map(p=>{ return p['id']}).indexOf(this.id);
    this.pic=this.photos[this.index];
  }
  getRating=(rating:number)=>{return `${rating*10}%`}

  addReview=()=>{
    if(this.name == null || this.name == undefined || this.name == '')
    this.toastr.warning('Name Is Required')
    else if(this.review == null || this.review == undefined || this.review == '')
    this.toastr.warning('Review is Required')
    else {
      let data:Review={ name:this.name,review:this.review,rating:this.rating }
      if(this.photos[this.index].reviews == undefined)
        this.photos[this.index]['reviews']=[];
      if(this.pic['rating']==undefined ||this.pic['rating']==null )
        this.pic['rating']=this.rating
      else
        this.pic['rating']=(this.pic['rating']+this.rating)/2;

      this.pic['reviews'].unshift(data);
      this.appService.setInfoInLocalStorage(this.photos)
      this.review='';this.name='';this.rating=0;
      this.toastr.success('Review has been added successfully')
    }
  }
}
