import { Component, OnInit } from '@angular/core';
import { AppService } from '../app.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  private skip=0;
  public photos:any=[];
  public load:boolean=false;
  constructor(private appService:AppService,private toastr: ToastrService) {  }

  ngOnInit() {  
    this.photos=this.appService.getInfoFromLocalStorage();
    if(this.photos==null || this.photos==undefined || this.photos.length==0)this.getPhotos()
  }
  getPhotos=()=>{
    let previousPhotos;
    if(this.photos==undefined || this.photos== null )previousPhotos=[];
    else previousPhotos=this.appService.getInfoFromLocalStorage();
    this.load=true;
    this.appService.fetchPhotos(this.skip+=1).subscribe((data)=>{
      if(data.stat=="ok"){
        this.photos=previousPhotos.concat(data.photos.photo)
        this.toastr.success('Photos has been fetched successfully!')
      }else if(data.stat=='fail'){
        this.photos=previousPhotos;
        this.toastr.warning(data.message)
      }setTimeout(() => { this.load=false }, 1400);
      this.appService.setInfoInLocalStorage(this.photos);
    })
  }
  getRating=(rating:number)=>{return `${rating*10}%`}
}
